import React from "react";
import "./App.css";

function App() {
  let currdate = new Date();
  let hrs = currdate.getHours();
  let greetings = "";

  const cssStyle = {};

  if (hrs >= 1 && hrs < 12) {
    greetings = "Good Morning";
    cssStyle.color = "#229954";
  } else if (hrs >= 12 && hrs < 16) {
    greetings = "Good Afternoon";
    cssStyle.color = "#F39C12";
  } else if (hrs >= 16 && hrs < 21) {
    greetings = "Good Evening";
    cssStyle.color = "#85929E";
  } else {
    greetings = "Good Night";
    cssStyle.color = "#17202A";
  }

  return (
    <>
      <div>
        <h1>
          Hello Sir ,<span style={cssStyle}>{greetings}</span>
        </h1>
      </div>
    </>
  );
}

export default App;
